export enum AudioType {
    JUMP,
    CRASH,
    HIGH_SCORE
}

const AUDIO_INFO: { [key in AudioType]: { src: string, volumn: number } } = {
    [AudioType.JUMP]: {
        src: 'data:audio/mpeg;base64,',
        volumn: 1
    },
    [AudioType.CRASH]: {
        src: 'data:audio/mpeg;base64,',
        volumn: 1,
    },
    [AudioType.HIGH_SCORE]: {
        src: 'data:audio/mpeg;base64,',
        volumn: 0.4,
    }
}

export interface IAudioPlayer {
    playSound(type: AudioType): void
}

export class AudioPlayer implements IAudioPlayer {
    private audioInfo: { [key in AudioType]: HTMLAudioElement }

    constructor() {
        this.audioInfo = {
            [AudioType.JUMP]: this.createAudio(AudioType.JUMP),
            [AudioType.CRASH]: this.createAudio(AudioType.CRASH),
            [AudioType.HIGH_SCORE]: this.createAudio(AudioType.HIGH_SCORE),
        }
    }

    createAudio(type: AudioType): HTMLAudioElement {
        const { src, volumn } = AUDIO_INFO[type]
        const audio = new Audio(src)
        audio.volume = volumn
        return audio
    }

    playSound(type: AudioType) {
        this.audioInfo[type].play()
    }
}
